import React, { useEffect, useRef, useState } from 'react';
import classnames from 'classnames/bind';

import { Spreadsheet as SpreadsheetBase } from 'dhx-spreadsheet-package';
import { IFormats } from 'dhx-spreadsheet-package/codebase/types/ts-spreadsheet/sources/types';
import 'dhx-spreadsheet-package/codebase/spreadsheet.css';

import styles from './demo.module.scss';

const cx = classnames.bind(styles);

function Demo() {
  const spreadSheetRef = useRef(null);
  const [spreadSheet, setSpreadSheet] = useState<SpreadsheetBase | null>(null);

  useEffect(() => {
    return () => {
      if (spreadSheet) {
        spreadSheet.destructor();
      }
    };
  }, [spreadSheet]);

  useEffect(() => {
    const spreadSheetElem = spreadSheetRef.current;

    if (spreadSheetElem && !spreadSheet) {
      setSpreadSheet(
        new SpreadsheetBase(spreadSheetElem, {
          toolbarBlocks: [],
        })
      );
    }
  }, [spreadSheetRef, spreadSheet]);

  const [serializedData, setSerializedData] = useState<{
    sheets: unknown[];
    styles: Record<string, unknown>;
    columns: Record<string, unknown>;
    formats: IFormats[];
  } | null>(null);

  const handleSaveToJsonButtonClick = () => {
    if (spreadSheet) {
      const serializedData = spreadSheet.serialize();

      setSerializedData(serializedData);
    }
  };

  const handleSaveToXlsxButtonClick = () => {
    if (spreadSheet) {
      spreadSheet.export.xlsx();
    }
  };

  const handleLoadButtonClick = () => {
    if (spreadSheet) {
      spreadSheet.load('', 'xlsx')
    }
  };

  return (
    <div className={styles.demo}>
      <p>
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Placeat perspiciatis repudiandae, sunt quo aliquam veritatis aut consectetur corporis. Voluptatum, sit ut tenetur
        velit accusamus quae iusto officiis omnis ea minus!
      </p>
      <div className={styles.demoSpreadSheet} ref={spreadSheetRef} />
      <p>
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Placeat perspiciatis repudiandae, sunt quo aliquam veritatis aut consectetur corporis. Voluptatum, sit ut tenetur
        velit accusamus quae iusto officiis omnis ea minus!
      </p>
      <p>
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Placeat perspiciatis repudiandae, sunt quo aliquam veritatis aut consectetur corporis. Voluptatum, sit ut tenetur
        velit accusamus quae iusto officiis omnis ea minus! Lorem ipsum dolor sit amet consectetur adipisicing elit. Placeat perspiciatis repudiandae, sunt quo aliquam veritatis
        aut consectetur corporis. Voluptatum, sit ut tenetur velit accusamus quae iusto officiis omnis ea minus!
      </p>

      <a href="https://docs.dhtmlx.com/spreadsheet/functions/#getting-cell-formula">  DHTMLX SpreadSheet</a>
      <br />
      
      <button className={cx(styles.demoBtn, styles.demoBtnRed)} onClick={handleLoadButtonClick}>
        Load from xlsx
      </button>
      <br />
      <button className={cx(styles.demoBtn, styles.demoBtnBrown)} onClick={handleSaveToJsonButtonClick}>
        Save to json
      </button>
      <button className={styles.demoBtn} onClick={handleSaveToXlsxButtonClick}>
        Save to xlsx
      </button>

      <pre>{serializedData && JSON.stringify(serializedData, null, 2)}</pre>
    </div>
  );
}

export default Demo;
